.aboutMain {
  margin-bottom: 130px;

  @media (max-width: $ds) {
    margin-bottom: 80px;
  }

  @media (max-width: $tb) {
    margin-bottom: 45px;
  }

  &__wrapper {
    @media (max-width: $tb) {
      padding: 0 15px;
    }
  }

  &__heading {
    text-align: center;
    margin-bottom: 66px;

    @media (max-width: $tb) {
      margin-bottom: 52px;
    }

    h2 {
      font-family: "CormorantGaramond";
      font-size: 3.4rem;
      font-weight: 700;
      color: $basicgray3;
      margin-bottom: 33px;
      letter-spacing: 1.2px;

      @media (max-width: $ds) {
        margin-bottom: 23px;
      }

      @media (max-width: $tb) {
        margin-bottom: 22px;
        margin-top: 68px;
      }
    }

    a {
      font-family: "Poppins";
      font-size: 1.6rem;
      font-weight: 500;
      color: $basicgray3;
      cursor: pointer;
      text-decoration: none;
      padding-bottom: 4px;
      border: 1px solid transparent;
      border-bottom: 1px solid $basicbrown;

      &:hover {
        opacity: 0.8;
      }

      &:active {
        opacity: 0.8;
      }

      &:focus {
        outline: 1px solid transparent;
        border: 1px solid $black;
      }

      &:visited {
        opacity: 0.6;
      }
    }
  }

  &__content {
    display: flex;
    justify-content: space-between;
    flex-wrap: nowrap;

    @media (max-width: $ds) {
      padding: 0 25px;
    }

    @media (max-width: $tb) {
      flex-wrap: wrap;
      min-height: 387px;
      padding: 0;
    }

  }

  &__content-item {
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    @media (max-width: $ds) {
      width: 19.5%;
    }

    @media (max-width: $tb) {
      width: 46%;
    }

    &:first-of-type {
      @media (max-width: $tb) {
        justify-content: flex-start;

        .aboutMain__content-image {
          &:first-of-type {
            margin-bottom: 18%;
          }
        }
      }
    }

    &:last-of-type {
      margin-left: 20px;

      @media (max-width: $ds) {
        margin-left: 0;
      }
    }
  }

  &__content-image {
    img {
      @media (max-width: $tb) {
        width: 100%;
      }
    }
  }

  &__content-itembigblock {
    margin-left: 20px;
    margin-right: 20px;

    @media (max-width: $ds) {
      margin: 0;
      width: 34%;
    }

    @media (max-width: $tb) {
      width: 46%;
    }
  }

  &__content-imageRings {

    @media (max-width: $tb) {
      display: none;
    }
  }

  &__content-imageRingsneckale {
    @media (max-width: $tb) {
      display: none;
    }
  }
}
